$blue: #5915e1;
$black: #333333, #666666, #999999;
$gray: #fafafa, #dbdbdb;
$orange: #ff8e18;
$green: #16c378;
$red: #e85d54;
$hoverBlue: #4711b4;
$hoverOrgange: #cc7113;
$superPurple: #5816e0;
$font333: #333;
$font666: #666;
$font999: #999;
$bgColor: #1d132e;

.mr-0 {
  margin-right: 0 !important;
}

// modal
.base-home-swapmodal .ant-modal-content,
.base-home-swapmodal .ant-modal-content,
.base-home-swapmodal .ant-modal.pool-modal .ant-modal-content {
  padding: 30px 45px !important;
  border-radius: 20px;
}

html,
body {
  background: $bgColor;
  font-family: AvenirNext-Regular;
  font-size: 14px;
  color: nth($black, 1);
  overscroll-behavior: none;

  .ib-parent {
    display: block;

    div,
    p,
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      display: inline-block;
    }
  }

  .ant-modal-mask,
  .ant-modal-wrap {
    z-index: 1001;
  }

  .toast-text {
    display: inline-block;
    // padding-left: 33px;
    // background: url(../images/laba.svg) left no-repeat;
    height: 40px;
    overflow: hidden;

    ul {
      display: block;
      height: 40px;
      line-height: 22px;
      margin: 0;
      position: relative;

      &.animate {
        -webkit-animation: 7.5s rowup linear infinite normal;
        animation: 7.5s rowup linear infinite normal;
      }
    }

    @keyframes rowup {
      0% {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
      }

      5% {
        -webkit-transform: translate3d(0, -40px, 0);
        transform: translate3d(0, -40px, 0);
      }

      33% {
        -webkit-transform: translate3d(0, -40px, 0);
        transform: translate3d(0, -40px, 0);
      }

      38% {
        -webkit-transform: translate3d(0, -80px, 0);
        transform: translate3d(0, -80px, 0);
      }

      66% {
        -webkit-transform: translate3d(0, -80px, 0);
        transform: translate3d(0, -80px, 0);
      }

      71% {
        -webkit-transform: translate3d(0, -120px, 0);
        transform: translate3d(0, -120px, 0);
      }

      100% {
        -webkit-transform: translate3d(0, -120px, 0);
        transform: translate3d(0, -120px, 0);
      }
    }

    a {
      color: #fff;
      line-height: 22px;
      padding: 9px 0;
      height: 40px;
      display: block;
      text-align: center;
    }

    a .link {
      font-family: PingFang SC;
      font-style: normal;
      font-weight: normal;
      font-size: 12px;
      line-height: 16px;
      color: #30084c;
      background: #ffffff;
      border-radius: 23px;
      padding: 2px 10px;
      margin-left: 15px;
      display: inline-block;
      height: 19px;
      vertical-align: top;
      margin-top: 2px;
    }
  }

  /* 提示问号样式通用 */
  .ques {
    color: nth($black, 3);
    display: inline-block;
    background: #fff;
    min-width: 18px;
    width: 18px;
    height: 18px;
    line-height: 18px;
    text-align: center;
    border-radius: 100%;
    // margin-left: 10px;
    margin-right: 10px;
    border: 1px solid nth($black, 3);
    font-family: AvenirNext-Medium;
    font-size: 16px;
    cursor: pointer;
  }

  /* 链接通用 */
  .link {
    color: $blue;
    cursor: pointer;
    font-family: AvenirNext-Medium;
    font-size: 14px;

    &:hover {
      color: $hoverBlue;
    }
  }

  /* modal通用 */
  .base-home-swapmodal {
    // max-width: 630px;
    transform: scale(0.8);
    text-align: center;
    width: 100%;

    .modal-success {
      img {
        width: 100px;
        margin-top: 25px;
      }
    }

    .title {
      margin: 40px 0;
      font-weight: 500;
      color: #333;
      font-family: PingFangSC-Regular;

      span {
        margin-right: 3px;

        &:last-child {
          margin-right: 0;
        }
      }

      &.big {
        margin: 0 0 5px;
        font-size: 20px;
      }

      &.normal {
        font-size: 15px;
        margin: 0 0 12.5px;
      }

      &.green {
        font-family: PingFangSC-Medium;
        font-size: 25px;
        color: #16c378;
        margin: 30px 0 15px;
      }
    }

    &.add-gai {
      .amount {
        .amount-num {
          font-family: AvenirNext-DemiBold;
          font-size: 30px;
          color: #5915e1;
        }
      }

      .price-value {
        text-align: right;
      }

      .price .param img {
        width: 20px;
        height: 20px;
        margin-right: 10px;
      }

      .amount-tip {
        font-family: AvenirNext-Medium;
        font-size: 14px;
        color: #999999;
        margin-top: 15px;
      }
    }

    .from-value,
    .to-value,
    .add-value,
    .remove-value {
      font-family: AvenirNext-Medium;
      font-size: 25px;
      color: #333333;
      letter-spacing: 0;
      text-align: right;
      display: inline-block;
      word-break: break-all;
    }

    .plus {
      font-family: AvenirNext-Medium;
      font-size: 17.5px;
      color: #5915e1;
      margin: 0 4px;
      top: -2px;

      &.w10 {
        width: 12.5px;
        margin: 0 12.5px;
        margin-top: -7px;
      }
    }

    .add-ver {
      font-family: Helvetica Neue;
      width: 42px;
      height: 20px;
      line-height: 20px;
      background: #f7f7f7;
      border-radius: 9px;
      font-size: 12px;
      color: rgba(26, 26, 26, 0.6);
      margin-left: 8px;
    }

    .from-token,
    .to-token,
    .add-token,
    .remove-token {
      font-family: AvenirNext-Medium;
      font-size: 14px;
      color: $font999;
      margin-top: 11px;
      margin-left: 4px;

      img {
        width: 20px;
        vertical-align: middle;
        display: none;
      }

      span {
        vertical-align: middle;
        line-height: 20px;
      }
    }

    .supplyLineTitle,
    .middleTip {
      margin-top: 24px;
      font-size: 14px;
      color: #666;
      margin-bottom: -10px;
    }

    .detail-item {
      padding: 10px 0px;
      font-family: AvenirNext-Regular;
      font-size: 14px;
      color: $font333;

      .green {
        color: $green;
      }

      .orange {
        color: $orange;
      }

      span:last-child {
        max-width: 240px;
        // text-align: right;
        text-align: left;
      }
      span.right {
        text-align: right;
      }
    }

    .recipient-addr {
      background: #ffffff;
      box-shadow: 0 10px 40px 0 rgba(21, 55, 156, 0.06);
      border-radius: 20px;
      padding: 20px;
      margin-bottom: 20px;
      font-family: AvenirNext-Medium;
      font-size: 14px;
      color: $font999;

      .address {
        font-family: AvenirNext-DemiBold;
        font-size: 16px;
        color: $font333;
        margin-top: 10px;
      }
    }

    .price,
    .detail-slippage {
      padding: 10px 0px;
      font-family: AvenirNext-Medium;
      font-size: 16px;
      color: $font333;
    }

    .warning {
      font-family: AvenirNext-Regular;
      font-size: 14px;
      color: #999999;
      padding: 20px 0;
      text-align: left;
    }

    .img {
      height: 50px;
      line-height: 50px;
    }

    .bold {
      font-size: 18px;
      font-weight: 600;
      color: #111;
      margin: 40px 0 20px;
    }

    .red {
      color: #f00;
      // margin-bottom: 30px;
    }

    .lastBtn {
      width: 70%;
      height: 50px;
      line-height: 50px;
      text-align: center;
      margin-top: 30px;
      background: #fff;
      border: 1px solid #666;
      border-radius: 5px;
      color: #555;
      cursor: pointer;
    }

    .amount {
      padding: 15px 0px 10px;
      align-items: center;
      border-bottom: 1px solid rgba(216, 216, 216, 0.5);

      &.pb25 {
        padding-bottom: 25px;
      }

      &.trans-modal {
        border-bottom: none;
      }

      .amount-num {
        font-family: AvenirNext-DemiBold;
        font-size: 30px;
        color: #333333;
        min-height: 27px;
        line-height: 27px;
      }

      .amount-name {
        margin-top: 9px;
        font-family: AvenirNext-Medium;
        font-size: 14px;
        color: #333;

        img {
          width: 20px;
          height: 20px;
          vertical-align: top;
        }

        span {
          margin-left: 10px;
        }
      }

      .symbol {
        margin: 0 30px;
        font-size: 30px;
      }
    }

    .tokens {
      padding-bottom: 0px;
      border-bottom: none;
    }

    .recipient {
      margin-top: 5px;
      display: block;
    }

    .endTip {
      opacity: 0.8;
      font-family: PingFangSC-Regular;
      font-size: 14px;
      color: #999999;
      margin-top: 17.5px;
      display: block;
    }

    &.confirm-swap-modal {
      .price {
        border-top: 1px solid rgba(216, 216, 216, 0.5);
        margin-top: 25px;
      }
    }

    .ant-modal-body {
      .price {
        font-size: 16px;
        padding-bottom: 0px;

        .right {
          font-size: 16px;
          color: #666;
        }
      }

      .detail-list {
        font-size: 16px;
        margin-top: -2.1px;

        span {
          font-size: 16px;
        }

        .detail-item > div > span:last-child {
          line-height: 1;
        }
      }
    }

    .supplyDescTitle {
      margin-top: 17.5px;
      opacity: 0.8;
      font-family: PingFangSC-Regular;
      font-size: 14px;
      color: #999999;
    }
  }

  .base-home-swapmodal {
    .ant-modal-body {
      border-radius: 5px;
      padding: 0 !important;

      .price {
        padding: 25px 0px 30px;

        .param {
          justify-content: space-between;

          &.flex {
            margin-bottom: 12.5px;
          }

          img {
            width: 25px;
            height: 25px;
            margin-right: 2px;

            &:nth-child(2) {
              margin: 0 15px 0 -8px;
            }
          }

          span {
            vertical-align: middle;
            font-family: AvenirNext-Medium;
            font-size: 16px;

            &.price-key {
              color: #333333;
              text-align: left;
            }
          }

          .price-value {
            text-align: right;

            p,
            span {
              font-family: AvenirNext-Medium;
              font-size: 16px;
              color: #666666;
            }

            .ml20 {
              margin-left: 20px;
            }
          }
        }

        .v-right {
          font-size: 16px;
          color: #666;
        }
      }

      .errorMsg {
        font-family: PingFangSC-Regular;
        font-size: 14px;
        color: #ff8e18;
        background: url(../images/failed.svg) left no-repeat;
        background-size: 13px;
        padding-left: 18px;
        display: inline-block;
        margin: 0 auto 10px;
      }

      .share-title {
        color: #333;
        font-size: 16px;
      }
    }

    .ant-modal-header {
      border: none;
      border-radius: 50px 50px 0 0;
      padding: 0;
      padding-bottom: 10px;

      .ant-modal-title {
        font-family: PingFangSC-Regular;
        font-size: 20px;
        color: #333333;
        text-align: center;
      }
    }

    .ant-modal-close {
      top: 30px;
      right: 45px;

      .ant-modal-close-x {
        width: 19px;
        height: auto;
        line-height: 0;
        font-size: 19px;
        font-weight: 800;
        color: #666;

        .anticon {
          background: url(../images/close_v2.svg) center center no-repeat;
          background-size: 19px 19px;
          width: 19px;
          height: 19px;

          svg {
            display: none;
          }
        }
      }
    }
  }

  .bold {
    font-weight: 600;
  }

  .fz12 {
    font-size: 12px;
  }

  .fz13 {
    font-size: 13px;
  }

  .fz14 {
    font-size: 14px;
  }

  .fz16 {
    font-size: 16px;
  }

  .black {
    color: nth($black, 1);
  }

  /* 通用标题 */
  .firstTitle {
    font-family: AvenirNext-DemiBold;
    font-size: 28px;
    color: #333333;
  }

  .secondTitle {
    font-family: AvenirNext-Medium;
    font-size: 20px;
    color: #333333;
  }

  /* 通用tooltip */
  .common-tool {
    margin: 20px 0;

    .ques {
      margin-right: 0 !important;
    }
  }

  .common-tool-title {
    font-family: AvenirNext-Medium;
    font-size: 14px;
    color: #333333;
  }

  .switch-version {
    .ant-tabs-content {
      position: relative;
      z-index: 2;
    }

    .ant-tabs-content-holder {
      position: relative;

      &::after {
        content: '';
        display: block;
        width: 100%;
        height: 240px;
        position: absolute;
        bottom: 0;
        right: 5px;
        z-index: 1;
        background-position: 0 0;
        background-repeat: no-repeat;
        background-size: 100%;
        border-radius: 0 0 50px 0;
      }
    }

    &.v1 {
      .ant-tabs-content-holder {
        // &::before {
        //   content: 'V1';
        //   font-size: 18px;
        //   color: #715fdf;
        //   background: rgba(113, 95, 223, 0.1);
        // }

        &::after {
          border-radius: 0;
          right: 15px;
          background: url(../images/v1-bg.svg) right no-repeat;
          background-size: auto 240px;
        }
      }
    }

    &.v15 {
      .ant-tabs-content-holder {
        &::after {
          background: url(../images/v15-bg.svg) right no-repeat;
          background-size: auto 240px;
        }
      }
    }

    &.v2 {
      .ant-tabs-content-holder {
        &::after {
          right: -1px;
          background: url(../images/v2-bg.svg) right no-repeat;
          background-size: auto 240px;
        }
      }
    }
  }
}

.typo-text-link {
  font-family: AvenirNext-Medium;
}

.ant-notification-notice {
