    }
  }

  .swap-content .swap-block .right-block,
  #swap-tab .action-content .token-sec .select {
    width: 55%;

    .input-max {
      display: inline-block;

      .link {
        vertical-align: top;
        margin-top: 4px;
        display: inline-block;
        margin-right: 5px;
      }
    }

    p:first-child {
      width: 100%;
    }

    .dragDown,
    p:first-child {
      display: inline-block;
      line-height: 30px;
      margin-top: 0;

      .balance {
        max-width: 150px;
        display: inline-block;
        vertical-align: middle;
      }
