@import './var.scss';
@import './mixins';

.home-page {
  overflow-x: hidden;
  background: url(../images/homeTokens/bg_page_home.png) no-repeat;
  background-size: 100% auto;
}
.home-page-content {
  margin: 60px auto 0;
  max-width: 1200px;
}

// hero start
.hero-bg {
  .hero {
    display: flex;
    .home-swap-box {
      position: relative;
      align-self: start;
      flex: 1;
      padding: 30px;
      background: #362d4a;
      border-radius: 20px;
      border: 1px solid rgba(255, 255, 255, 0.1);

      .hero-swap-box-card {
        .header-box {
          position: relative;
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-bottom: 25px;
          color: #fff;

          .box-header {
            font-weight: 700;
            font-size: 22px;
            line-height: 30px;
          }
          .box-tools {
            .box-tool-item {
              width: 28px;
              height: 28px;
            }

            img {
              display: block;
              width: 100%;
              cursor: pointer;
            }
          }
        }

        .box-body {
          display: flex;
          flex-direction: column;
          position: relative;

          .input-part {
            .info-row {
              display: flex;
              align-items: center;
              flex-direction: row;

              .view-token {
                margin-left: 20px;
                width: 28px;
                height: 28px;
                img {
                  display: block;
                  width: 100%;
                  height: 100%;
                  cursor: pointer;
                }
              }

              .balance-wrap {
                display: flex;
                align-items: center;
                flex: 1;
                justify-content: flex-end;
                width: 0;
                flex-wrap: wrap;
              }
              .balance-text {
                font-weight: 400;
                color: rgba(255, 255, 255, 0.5);
              }

              .balance-value {
                font-weight: 400;
                color: #ffffff;
                margin-left: 6px;
              }
            }

            .swap-input-wrap {
              position: relative;
              margin-top: 12px;
              background: #403858 !important;
              // overflow: hidden !important;
              border-radius: 10px;
              .max-btn {
                position: absolute;
                right: 10px;
                top: 19px;
                background: #251f32;
                border-radius: 15px;
                border: 1px solid rgba(255, 255, 255, 0.1);
                font-weight: 500;
                color: #fff;
                padding: 4px 14px;
                font-size: 12px;
                line-height: 17px;
                text-transform: uppercase;
                cursor: pointer;
              }
            }
            .max-input-wrap {
              padding-right: 65px;
            }

            .ant-input {
              height: 64px !important;
              white-space: nowrap;
              overflow: hidden !important;
              text-overflow: ellipsis;
              padding-left: 16px;
              padding-right: 16px;
              background: transparent !important;
              border: none !important;
              font-weight: 700;
              font-size: 30px;
              line-height: 36px;
              color: #ffffff !important;

              &::placeholder {
                color: rgba(255, 255, 255, 0.3);
              }

              &.to-input {
                cursor: not-allowed;
                caret-color: transparent;
              }
            }
          }

          .input-usd {
            padding: 0 16px 16px;
            text-align: left;
            color: #fff;
            font-size: 10px;
            font-weight: 600;
            opacity: 0.7;
          }

          .bottom-price-loading {
            padding: 0 16px 16px;
            font-size: 12px;
            font-weight: 600;
            color: rgba(255, 255, 255, 0.5);
            line-height: 17px;
            text-align: left;
          }
          .price-loading-img {
            position: relative;
            &:after {
              animation: loadingPoint 2000ms infinite;
              position: absolute;
              content: '';
              left: 10px;
              top: 50%;
              transform: translateY(-50%);
              letter-spacing: 3px;
              font-size: 15px;
            }
          }
        }

        .box-footer {
          .footer-info-row {
            display: flex;
            margin-top: 17px;
            margin-bottom: 8px;
            font-size: 14px;
            line-height: 20px;

            .swap-rate {
              display: flex;
              align-items: center;
              text-align: left;
              color: #fff;
              .swap-rate-label {
                color: rgba(255, 255, 255, 0.5);
              }
              .swap-rate-label1 {
                // margin-right: 4px;
              }
            }

            .purple-switch {
              margin-left: 12px;
              cursor: pointer;
              height: 22px;
              width: 22px;
            }
          }

          .submit-btn {
            margin-top: 30px;
            background: #8c51ff;
            font-style: normal;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 15px;
            font-weight: 700;
            font-size: 18px;
            line-height: 25px;
            width: 100%;
            user-select: none;
            height: 55px;
            border-radius: 55px;
            font-weight: 600;
            text-align: center;
            color: #fff;

            &.disabled {
              color: rgba(255, 255, 255, 0.4) !important;
              cursor: not-allowed;
            }

            &.blue {
              background: transparent !important;
              color: #fff !important;
            }

            &.gray {
              cursor: auto;
              color: #fff;
              background: #dbdbdb;
            }

            &.red {
              background: #e85d54 !important;
              color: #fff;
            }

            &.orange {
              background: #ff8e18;
              color: #fff;
            }
          }

          .error-text {
            font-size: 15px;
            font-weight: bold;
            color: #ff355d;
            line-height: 18px;
            margin-top: 20px;
          }
        }
      }

      .swap-input-wrap-tooltip {
        .ant-tooltip-inner {
          background: rgba(255, 255, 255, 0.1) !important;
          backdrop-filter: blur(5px) !important;
        }
      }
    }

    .dragDown {
      display: flex;
      align-items: center;
      justify-content: center;
      width: auto;
      height: 28px;
      cursor: pointer;

      .selector {
        display: flex;
        align-items: center;
        padding-right: 22px;
        background: url(../images/homeTokens/ico_token_select.svg) 100% no-repeat;
        background-position: 100% 50%;

        img {
          width: 20px;
          height: 20px;
          vertical-align: middle;
        }
        span {
          position: relative;
          line-height: 22px;
          font-family: PingFangSC-Semibold, PingFang SC;
          font-size: 16px;
          color: #fff;
          vertical-align: middle;
          display: inline-block;
          margin: 0 0 0 5px;
        }
      }
    }

    .box-control {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 24px 0;

      .swap-ico-wrap {
        margin-right: 10px;
        width: 46px;
        height: 46px;
        &:hover {
          .swap-ico-default {
            display: none;
          }
          .swap-ico-active {
            display: block;
          }
        }
        img {
          display: block;
          width: 100%;
          height: 100%;
          cursor: pointer;
        }
        .swap-ico-active {
          display: none;
        }
      }

      .trade-router-loading {
        color: #9482f8;
        margin-right: 40px;
        font-size: 15px;
      }
    }

    .safe-amount-tip {
      margin-top: 14px;
      padding: 0 2px;
      line-height: 21px;
      text-align: left;
      font-size: 15px;
      color: #fff;
      b {
        font-weight: 600;
        color: #c1a0ff;
      }
      .safe-word {
        color: #fff;
        opacity: 0.5;
      }
      .safe-btn {
        color: #c1a0ff;
        cursor: pointer;
      }
    }

    .box-description {
      margin-top: 8px;
    }

    .description-list {
      .description-list-item {
        margin-bottom: 8px;
        font-size: 14px;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: #ffffff;
        line-height: 20px !important;
        text-align: left;
        &:last-child {
          margin-bottom: 0;
        }
      }

      .label {
        opacity: 0.5;
        // margin-right: 8px;
      }
      .text {
        font-weight: bold;
      }
    }

    .hero-intro-card {
      width: 622px;
      margin-left: 18px;
      font-family: PingFangSC-Regular, PingFang SC;
      .title {
        padding-left: 42px;
        margin-bottom: 19px;
        color: #fff;
        font-weight: 800;
        font-size: 40px;
        line-height: 44px;
        background: linear-gradient(180deg, #ffffff 0%, #c5aaff 100%);
        background-clip: text;
        color: transparent;
      }

      .content {
        padding-left: 42px;
        text-align: left;
        margin-top: 8px;
        font-weight: 400;
        font-size: 16px;
        line-height: 26px;
        color: rgba(255, 255, 255, 0.7);
      }

      .banner-wrap {
        position: relative;
        margin-top: 19px;
        height: 400px;
        .banner-wrap-icon {
          z-index: 2;
          position: absolute;
          left: 270px;
          top: -45px;
          width: 240px;
        }
        .banner-wrap-bg {
          z-index: 3;
          position: absolute;
          left: 0;
          top: 0;
          width: 130%;
        }
        .rotate-wrap {
          position: absolute;
          left: 20px;
          top: 20px;
          width: 100px;
          height: 100px;
          transform-style: preserve-3d;
          animation: coinRotate 5s linear infinite;
          .rotate-item-front,
          .rotate-item-reverse {
            width: 100%;
            height: 100%;
            position: absolute;
            left: 0;
            top: 0;
            background-size: 100% 100%;
            background-repeat: no-repeat;
            border-radius: 50%;
          }
          .rotate-item-front {
            transform: translateZ(3px);
            background-image: url('https://picsum.photos/id/935/100/100');
          }
          .rotate-item-reverse {
            background-image: url('https://picsum.photos/id/835/100/100');
          }
        }
      }
    }
  }
}

@keyframes loadingPoint {
  25% {
    content: '·';
  }
  50% {
    content: '··';
  }
  75% {
    content: '···';
  }
  100% {
    content: '';
  }
}

@keyframes coinRotate {
  0% {
    transform: rotateY(0deg);
  }
  100% {
    transform: rotateY(360deg);
  }
}
// hero end

// tvl start
.tvl-card-wrap {
  position: relative;
  z-index: 3;
  display: flex;
  margin-top: 60px;
  .tvl-card-item {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    flex: 1;
    width: 0;
    margin-right: 20px;
    background: rgba(255, 255, 255, 0.05);
    border-radius: 10px;
    border: 1px solid rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(10px);
    &:last-child {
      margin-right: 0;
    }
    .tvl-card-title {
      padding: 20px 24px 0;
      font-size: 14px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: rgba(255, 255, 255, 0.5);
      line-height: 20px;
    }
    .tvl-card-text {
      flex: 1;
      margin: 10px 0;
      padding: 0 24px;
      font-size: 30px;
      font-family: Helvetica-Bold, Helvetica;
      font-weight: bold;
      color: #ffffff;
      line-height: 36px;
      .tvl-count-up {
        display: block;
        &::first-letter {
          margin-right: 6px;
        }
      }
    }
    .tvl-card-img {
      display: block;
      justify-self: end;
      width: 100%;
    }
  }
}
// tvl end

// htxbanner start
.home-htx-banner {
  margin: 194px auto 0;
  .home-htx-container {
    position: relative;
    background: url(../images/homeTokens/htx/home_banner_bg.png) no-repeat;
    background-size: 100% 100%;
    border-radius: 10px;
    cursor: pointer;
  }
  .home-htx-body {
    padding: 70px 190px 60px 80px;
    color: #fff;
    .home-htx-title,
    .home-htx-sub {
      font-family: PingFangSC, PingFang SC;
      font-weight: 600;
      font-size: 50px;
      line-height: 72px;
      font-style: normal;
    }
    .home-htx-desc {
      margin-top: 16px;
      font-family: PingFangSC, PingFang SC;
      font-weight: 400;
      font-size: 24px;
      color: #ffffff;
      line-height: 33px;
      font-style: normal;
      span {
        vertical-align: middle;
      }
      b {
        font-size: 30px;
        color: rgba(255, 172, 0, 1);
        vertical-align: text-bottom;
      }
      img {
        display: inline-block;
        height: 34px;
        vertical-align: middle;
      }
    }
    .home-htx-time {
      margin-top: 20px;
      font-family: PingFangSC, PingFang SC;
      font-weight: 400;
      font-size: 16px;
      color: rgba(255, 255, 255, 0.7);
      line-height: 22px;
      font-style: normal;
    }
    .home-htx-btm {
      display: flex;
      align-items: center;
      margin-top: 50px;
      .home-htx-icon {
        margin-right: 35px;
        width: 112px;
        img {
          display: block;
          width: 100%;
        }
      }
      .home-htx-btn {
        padding: 16px 21px;
        font-family: PingFangSC, PingFang SC;
        font-weight: 600;
        font-size: 20px;
        color: #2c3039;
        line-height: 28px;
        font-style: normal;
        background: linear-gradient(142deg, #ffb02c 0%, #ff783e 100%);
        border-radius: 30px;
        cursor: pointer;
        user-select: none;
      }
    }
  }
  .home-htx-bg1 {
    position: absolute;
    left: 307px;
    top: -60px;
    width: 180px;
    display: block;
  }
  .home-htx-bg2 {
    position: absolute;
    right: 0;
    top: -89px;
    width: 492px;
    display: block;
  }
  .home-htx-bg3 {
    position: absolute;
    left: 504px;
    bottom: -24px;
    width: 83px;
    display: block;
  }
  .home-htx-bg4 {
    position: absolute;
    right: 166px;
    bottom: -33px;
    width: 229px;
    display: block;
  }
}
// htxbanner end

// 挖矿start
.apy-section {
  margin: 100px auto 0;
  .slogan {
    margin-bottom: 50px;
    text-align: center;
    .title {
      margin-bottom: 20px;
      font-size: 30px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: #ffffff;
      line-height: 42px;
    }

    .content {
      font-size: 20px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: rgba(255, 255, 255, 0.5);
      line-height: 28px;
    }
  }

  .farm-apy-wrapper {
    display: flex;
    .farm-apy-item {
      position: relative;
      margin-right: 20px;
      padding: 0 20px 30px;
      flex: 1;
      width: 0;
      border: 1px solid rgba(255, 255, 255, 0.1);
      background: #362d4a;
      border-radius: 10px;
      &:last-child {
        margin-right: 0;
      }

      .hot-img {
        display: none;
      }
      &.farm-apy-hot {
        .hot-img {
          display: block;
          position: absolute;
          left: -3px;
          top: -3px;
          width: 64px;
          height: 64px;
        }
      }
    }

    .farm-apy-name {
      padding: 20px 0;
      font-size: 14px;
      font-weight: 600;
      color: rgba(255, 255, 255, 0.7);
      line-height: 20px;
      border-bottom: 1px solid rgba(231, 233, 237, 0.1);
      text-align: center;
    }

    .version-corner {
      background: rgba(255, 189, 174, 0.27);
      font-weight: 600;
      font-size: 18px;
      line-height: 22px;
      /* identical to box height */

      display: flex;
      align-items: center;
      text-align: center;

      color: #ff8c38;
      position: absolute;
      right: 0;
      top: 0;
      padding: 3px 13px 5px 14px;
      border-top-right-radius: 20px;
      border-bottom-left-radius: 20px;

      &.version1 {
        background: rgba(39, 224, 180, 0.16);
        color: #27e0b4;
      }
    }

    .apy-upper-part {
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;

      .token-icons {
        margin: 24px auto 16px;

        img {
          height: 32px;
        }
      }

      .symbol-text {
        display: flex;
        align-items: center;
        font-size: 16px;
        line-height: 22px;
        color: #ffffff;
        height: 22px;
        font-weight: 600;
        .apy-hot-icon {
          display: block;
          margin-right: 8px;
          width: 15px;
          height: 17px;
        }
      }
    }

    .apy-lower-part {
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;
      .apy-up-to-text {
        margin-top: 30px;
        text-align: center;

        .apy-up-to {
          color: rgba(255, 255, 255, 0.5);
          font-weight: 400;
          font-size: 14px;
          line-height: 20px;
          align-self: center;
          margin: auto;
        }

        .apy-text {
          margin-top: 10px;
          display: flex;
          align-items: center;
          text-align: center;
          color: #ffffff;
          height: 48px;
          font-weight: bold;
          font-size: 38px;
          line-height: 48px;
        }
      }

      .apy-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        font-family: PingFangSC-Semibold, PingFang SC;
        font-weight: 700;
        font-size: 14px;
        line-height: 17px;
        background: #8c51ff;
        border-radius: 40px;
        margin: 30px auto 0;
        width: 100%;
        color: #fff;
        padding: 12px auto;
        height: 40px;
      }
    }
  }
}
// 挖矿end

// 治理start
.gov {
  margin: 96px auto 100px;
  .box {
    display: flex;
    .box-r {
      flex: 1;
      .title {
        margin-bottom: 20px;
        padding-top: 60px;
        font-size: 30px;
        font-family: PingFangSC-Semibold, PingFang SC;
        font-weight: 600;
        color: #ffffff;
        line-height: 42px;
      }

      .content {
        font-size: 20px;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: rgba(255, 255, 255, 0.5);
        line-height: 34px;
        span {
          color: #fff;
          font-weight: 600;
        }
        .content-row {
          display: inline;
        }
      }
      .gov-btn {
        display: inline-block;
        margin-top: 50px;
        padding: 15px 38px;
        font-size: 18px;
        font-family: PingFangSC-Semibold, PingFang SC;
        font-weight: 600;
        color: #ffffff;
        line-height: 25px;
        background: #8c51ff;
        border-radius: 30px;
      }
    }
    .box-l {
      margin-left: 14px;
      margin-right: 30px;
      width: 374px;
      img {
        display: block;
        width: 100%;
        height: auto;
        align-self: flex-start;
      }
    }
  }
}

// 治理end

.mobile-wrap {
  display: none;
}

@media screen and (max-width: 1201px) {
  .home-page-content {
    padding: 0 20px;
  }
}

// 移动端适配 start
@media screen and (max-width: 1079px) {
  .pc-wrap {
    display: none;
  }
  .mobile-wrap {
    display: block;
  }

  .home-page-content {
    max-width: 100%;
    margin: 0 auto;
    padding: 0 15px;
  }

  // hero
  .hero-bg {
    .hero {
      flex-direction: column-reverse;
      margin-bottom: 24px;
      .home-swap-box {
        z-index: 5;
        position: relative;
        flex-shrink: 0;
        margin: 0 0 15px;
        padding: 10px;
        border-radius: 10px;
        align-self: initial;

        .hero-swap-box-card {
          .header-box {
            margin-bottom: 12px;

            .box-header {
              font-size: 18px;
