            }
            .box-tools {
              .box-tool-item {
                width: 24px;
                height: 24px;
                font-size: 12px;
                .new_setting_icon {
                  width: 24px;
                  height: 24px;
                  line-height: 24px;
                }
              }
            }
          }

          .box-body {
            .input-part {
              .info-row {
                .view-token {
                  margin-left: 10px;
                  width: 24px;
                  height: 24px;
                }

                .balance-wrap {
                  font-size: 12px;
                  line-height: 17px;
                }
              }

              .swap-input-wrap {
                margin-top: 8px;
                .max-btn {
                  top: 11px;
                  padding: 3px 12px;
                  font-size: 11px;
