                }
              }
              .max-input-wrap {
                padding-right: 60px;
              }

              .ant-input {
                height: 44px !important;
                padding-left: 10px;
                padding-right: 10px;
                font-size: 20px;
                line-height: 24px;
              }
            }

            .input-usd {
              padding: 0 10px 10px;
              font-size: 12px;
              line-height: 14px;
            }

            .bottom-price-loading {
              padding: 0 10px 10px;
              font-size: 12px;
              line-height: 14px;
            }
            .price-loading-img {
              position: relative;
              &:after {
                left: 5px;
                letter-spacing: 1px;
                font-size: 12px;
              }
            }
          }

          .box-footer {
            .footer-info-row {
              margin-top: 13px;
              margin-bottom: 10px;
              font-size: 12px;
              line-height: 17px;
              .purple-switch {
                margin-left: 10px;
                height: 20px;
                width: 20px;
              }
            }

            .submit-btn {
              margin-top: 20px;
              border-radius: 40px;
              font-size: 16px;
              line-height: 22px;
              height: 40px;
              border-radius: 55px;
            }

            .error-text {
              font-size: 12px;
              line-height: 17px;
              margin-top: 10px;
            }
          }
        }
      }

      .debug-box {
        .main-box {
          width: 100%;
          margin: 10px 0 0;
        }
      }

      .dragDown {
        height: 24px;

        .selector {
          padding-right: 20px;
          span {
            line-height: 20px;
            font-size: 14px;
          }
        }
      }

      .box-control {
        padding: 16px 0;

        .swap-ico {
          margin-right: 10px;
          width: 30px;
          height: 30px;
        }

        .swap-ico-wrap {
          width: 30px;
          height: 30px;
          .swap-ico-default {
            display: block !important;
          }
          .swap-ico-active {
            display: none !important;
          }
        }

        .trade-router-loading {
          margin-right: 30px;
          font-size: 12px;
        }
      }

      .safe-amount-tip {
        margin-top: 10px;
        line-height: 16px;
        font-size: 12px;
      }

      .box-description {
        margin-top: 10px;
      }

      .description-list {
        .description-list-item {
          margin-bottom: 10px;
          font-size: 12px;
          line-height: 17px !important;
          &:last-child {
            margin-bottom: 0;
          }
        }

        .label {
          margin-right: 6px;
        }
      }

      .hero-intro-card {
        margin: 15px 0 30px;
        width: 100%;
        .title {
          padding-left: 0;
          margin-bottom: 14px;
          font-size: 26px;
          line-height: 30px;
        }
        .content {
          padding-left: 0;
          margin-top: 9px;
          font-size: 12px;
          line-height: 18px;
        }

        .banner-wrap {
          display: none;
        }
      }
    }
