  // 挖矿
  .apy-section {
    margin: 40px 0 0;
    .slogan {
      margin-bottom: 20px;
      .title {
        margin-bottom: 8px;
        font-size: 20px;
        line-height: 28px;
      }

      .content {
        font-size: 14px;
        line-height: 20px;
      }
    }

    .farm-apy-wrapper {
      flex-direction: column;

      .farm-apy-item {
        flex: initial;
        padding: 0 12px;
        width: 100%;
        margin-bottom: 10px;
        margin-right: 0;
        &.farm-apy-hot {
          .hot-img {
            left: -1px;
            top: -1px;
            width: 30px;
            height: 30px;
          }
        }
        .farm-apy-header {
          display: flex;
          align-items: center;
          padding: 12px 0;
          border-bottom: 1px solid rgba(231, 233, 237, 0.1);
          img {
            height: 20px;
          }
          .symbol-text {
            display: flex;
            align-items: center;
            flex: 1;
            margin-left: 10px;
            font-size: 14px;
            line-height: 20px;
            color: #fff;
            .apy-hot-icon {
              margin-left: 10px;
              margin-right: 10px;
              width: 15px;
              height: 17px;
            }
          }
          .farm-pool-version {
            color: rgba(255, 255, 255, 0.7);
          }
        }

        .farm-apy-btm {
          display: flex;
          align-items: center;
          padding: 14px 0;
          .farm-apy-btm-l {
            display: flex;
            align-items: center;
            flex: 1;
            .apy-up-to {
              margin-right: 8px;
              color: rgba(255, 255, 255, 0.5);
            }
            .apy-text {
              color: #fff;
              font-size: 20px;
              line-height: 24px;
            }
          }
          .apy-btn {
            padding: 5px 16px;
            font-size: 14px;
            font-family: PingFangSC-Semibold, PingFang SC;
            font-weight: 600;
            line-height: 20px;
            color: #fff;
            border-radius: 30px;
            background: #8c51ff;
            cursor: pointer;
          }
        }
      }
    }
  }

  .gov {
    margin: 40px auto 0;
    width: 100%;
    .box {
      display: flex;
      flex-direction: column-reverse;
      .box-r {
        margin: 0;
        .title {
          margin-bottom: 13px;
          padding: 0;
          font-size: 20px;
          line-height: 28px;
        }
        .content {
          font-size: 14px;
          line-height: 24px;
        }
        .gov-btn {
          z-index: 10;
          position: relative;
          margin-top: 24px;
          padding: 5px 20px;
          border-radius: 30px;
          font-size: 14px;
          line-height: 20px;
        }
      }

      .box-l {
        display: flex;
        justify-content: flex-end;
        margin-left: 0;
        margin-right: 0;
        margin-top: -15px;
        width: 100%;
        img {
          width: 260px;
        }
      }
    }
  }
